<template>
  <div class="ft-register">
    <div
      class="ft-register__section d-flex justify-content-center align-items-center"
    >
      <div class="ft-register__section--item col-md-12">
        <p class="ft-register__title text-center mt-5">
          Regístrate en
          <strong>Fit Training</strong>
        </p>
        <div class="card card-container">
          <!-- <img
          id="profile-img"
          src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
          class="profile-img-card"
          alt="avatar"
          /> -->
          <!-- <img
            id="profile-img"
            src="@/assets/img/fit-training-logo2.png"
            class="profile-img-card"
            alt="avatar"
          /> -->
          <form name="form" @submit.prevent="handleRegister">
            <div v-if="!successful">
              <div class="form-group">
                <label for="username">Nombre de usuario</label>
                <input
                  v-model="user.username"
                  v-validate="'required|min:3|max:20'"
                  type="text"
                  class="form-control"
                  name="username"
                />
                <div
                  v-if="submitted && errors.has('username')"
                  class="alert-danger"
                >
                  {{ errors.first('username') }}
                </div>
              </div>
              <div class="form-group">
                <label for="email">Email</label>
                <input
                  v-model="user.email"
                  v-validate="'required|email|max:50'"
                  type="email"
                  class="form-control"
                  name="email"
                />
                <div
                  v-if="submitted && errors.has('email')"
                  class="alert-danger"
                >
                  {{ errors.first('email') }}
                </div>
              </div>
              <div class="form-group">
                <label for="password">Contraseña</label>
                <input
                  v-model="user.password"
                  v-validate="'required|min:6|max:40'"
                  type="password"
                  class="form-control"
                  name="password"
                />
                <div
                  v-if="submitted && errors.has('password')"
                  class="alert-danger"
                >
                  {{ errors.first('password') }}
                </div>
              </div>
              <div class="form-group">
                <button class="btn btn-primary btn-block">Crear cuenta</button>
              </div>
            </div>
          </form>

          <div
            v-if="message"
            class="alert"
            :class="successful ? 'alert-success' : 'alert-danger'"
          >
            {{ message }}
          </div>
        </div>

        <div class="col-md-12 text-center">
          <span class="mr-1">¿Ya tienes una cuenta?</span>
          <router-link class="router-link" :to="{ name: 'login' }"
            >Inicia sesión</router-link
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import User from '../../models/user';

export default {
  name: 'FtRegister',
  data() {
    return {
      user: new User('', '', ''),
      submitted: false,
      successful: false,
      message: ''
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    }
  },
  mounted() {
    if (this.loggedIn) {
      this.$router.push('/routine/new');
    }
  },
  methods: {
    handleRegister() {
      this.message = '';
      this.submitted = true;
      this.$validator.validate().then(isValid => {
        if (isValid) {
          this.$store.dispatch('auth/register', this.user).then(
            data => {
              this.message = data.message;
              this.successful = true;
            },
            error => {
              this.message =
                (error.response && error.response.data) ||
                error.message ||
                error.toString();
              this.successful = false;
            }
          );
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.ft-register {
  &__section {
    height: calc(100vh - #{$height-navbar});
  }

  &__title {
    font-size: 2em;
    strong {
      color: $mandarine;
      font-weight: bold;
    }
  }
}
label {
  display: block;
  margin-top: 10px;
}

.card-container.card {
  max-width: 350px !important;
  padding: 40px 40px;
}

.card {
  background-color: #f7f7f7;
  padding: 20px 25px 30px;
  margin: 0 auto 25px;
  margin-top: 50px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.profile-img-card {
  width: 96px;
  height: 96px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
</style>
